<template>
  <div class="layout">
    <nav-bar></nav-bar>
    <div class="container">
      <mu-row gutter>
        <mu-col width="100" tablet="100" desktop="100">
          <mu-paper  :zDepth="2" >
            <div class="title">
              <h1><font-awesome-icon :icon="['far','comment']"/> /</h1>
            </div>
            <div class="description">
              <div>
                <span></span>
                <select class="select" name="" id="" v-model="value">
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                  <option value="5"></option>
                </select>
                <input class="input" type="text" placeholder="">
              </div>
              <div class="mark-description">
                <ul>
                  <li><a href="https://github.com/sparanoid/chinese-copywriting-guidelines"></a></li>
                  <li> Markdown , ****~~~~``,  <a href="https://github.com/riku/Markdown-Syntax-CN/blob/master/syntax.md">Markdown</a> </li>
                  <li>, ,  - jpg, png, gif</li>
                  <li></li>
                </ul>
              </div>
            </div>
            <div class="editor">
              <mavon-editor id="edit" ref="md" @imgAdd="$imgAdd" @imgDel="$imgDel" v-model="input" :ishljs = "true"></mavon-editor>
            </div>
            <div class="editor">
              <mu-raised-button  class="button" label=" " secondary fullWidth/>
            </div>
          </mu-paper>
        </mu-col>
      </mu-row>
    </div>
  </div>
</template>

<script>
  import NavBar from '../Navber'
  export default {
    name: "create",
    data() {
      return {
        value: '1',
        input: ''
      }
    },
    components: {
      NavBar
    },
    methods: {
      handleChange (value) {
        this.value = value
      },
      $imgDel(pos){
        delete this.img_file[pos];
      },
      // @imgAdd event
      $imgAdd(pos, $file){
        // ..
        var formdata = new FormData();
        formdata.append('image', $file);
        axios({
          url: '/upload',
          method: 'post',
          data: formdata,
          headers: { 'Content-Type': 'multipart/form-data' },
        }).then((url) => {
          // .url![...](./0) -> ![...](url)
          /**
           * $vm mavonEditor
           * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
           * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
           */
          this.$refs.md.$img2Url(pos, url);
        })
      }
    }
  }
</script>

<style scoped>
  #edit{
    min-height: 500px;
  }
  .container{
    max-width: 1300px;
    width: 90%;
    margin: 0 auto;
  }
  .layout{
    background-color: rgb(236, 236, 236);
  }
  .title {
    margin: 30px;
    padding: 30px 30px 0 30px;
    border-bottom: 1px solid #ccc;
    text-align: center;
  }
  .editor {
    margin: 30px;
    padding-bottom: 30px;
  }
  .description {
    margin: 0 30px 0 30px;
  }
  .mark-description{
    width: 100%;
    margin: 20px 20px 0 0 ;
    border: 1px solid #ccc;
    color: #606060;
  }
  input[type="text"]{
    box-sizing: border-box;
    border-radius:4px;
    line-height: 25px;
    border:1px solid #c8cccf;
    color:#6a6f77;
    -web-kit-appearance:none;
    -moz-appearance: none;
    outline:0;
    padding:0 1em;
    text-decoration:none;
    width:80%;
  }
  input[type="text"]:focus{
    border:1px solid #ff7496;
  }
</style>



