<template>
  <mu-col width="100" tablet="70" desktop="70">
    <mu-paper class="article"  :zDepth="2" >
      <div class="profile">
        <h1></h1>
        <mu-divider/>
        <div class="edit-profile">
          <span class="field"></span>
          <font-awesome-icon icon="user"/>
          <mu-text-field label="Username" labelFloat/><span class="description">Acorns</span><br/>
          <span>   </span>
          <font-awesome-icon icon="transgender"/>
          <mu-select-field v-model="gander" label="Gander">
            <mu-menu-item value="1" title=""/>
            <mu-menu-item value="2" title=""/>
          </mu-select-field><span class="description"></span><br/>
          <label>Github</label>
          <font-awesome-icon :icon="['fab', 'github']" />
          <mu-text-field label="Github Name" labelFloat/><span class="description">Panda</span><br/>
          <label>Email </label>
          <font-awesome-icon :icon="['fas','envelope']" />
          <mu-text-field label="Email" labelFloat/><span class="description">acorndu@gmail.com</span><br/>
          <label>   </label>
          <font-awesome-icon :icon="['fas','university']" />
          <mu-text-field label="City" labelFloat/><span class="description"></span><br/>
          <label>   </label>
          <font-awesome-icon :icon="['fas','universal-access']" />
          <mu-text-field label="Name" labelFloat/><span class="description">Taylor</span><br/>
          <label>    </label>
          <font-awesome-icon :icon="['fas','building']" />
          <mu-text-field label="Company" labelFloat/><span class="description">Google</span><br/>
          <label>    </label>
          <mu-text-field hintText="Describe" multiLine :rows="3" :rowsMax="6" /><span class="description"></span><br/>
        </div>
        <div class="profile-button">
          <mu-raised-button class="demo-raised-button" backgroundColor="#DB6862" label="" icon="save" fullWidth default/>
        </div>
      </div>
    </mu-paper>
  </mu-col>
</template>

<script>
  import FontAwesomeIcon from '@fortawesome/vue-fontawesome';

  export default {
    name: "profile",
    components: {FontAwesomeIcon}
  }
</script>

<style scoped>
  .profile{
    margin: 0 20px 0 20px;
  }
  .profile h1{
    padding: 20px 0 0 0;
  }
  .description{
    color: #ccc;
    font-size: 12px;
  }
  .edit-profile {
    padding: 0 0 20px 0;
  }
  .profile-button{
    padding: 10px 0 20px 0;
  }
</style>



